import _fetch from '@/utils/axios'

// 
function getDetailsApi (id) {
  return _fetch({
    url: `/v1_0/articles/${id}`,
    headers: {
      contentType: 'application/json'
    }
  })
}
// 
function getCommentsApi (data) {
  return _fetch({
    url: '/v1_0/comments',
    headers: {
      contentType: 'application/json'
    },
    params: data
  })
}
// 
function unfollowUsersApi (id) {
  return _fetch({
    url: `/v1_0/user/followings/${id}`,
    method: 'DELETE',
    headers: {
      contentType: 'application/json'
    }
  })
}
// 
function followUsersApi (data) {
  return _fetch({
    url: '/v1_0/user/followings',
    method: 'POST',
    headers: {
      contentType: 'application/json'
    },
    data
  })
}
// 
function likingsApi (data) {
  return _fetch({
    url: '/v1_0/article/likings',
    method: 'POST',
    headers: {
      contentType: 'application/json'
    },
    data
  })
}

// 
function deleteLikingApi (id) {
  return _fetch({
    url: `/v1_0/article/likings/${id}`,
    method: 'DELETE',
    headers: {
      contentType: 'application/json'
    }
  })
}

// 
function distasteApi (data) {
  return _fetch({
    url: '/v1_0/article/dislikes',
    method: 'POST',
    headers: {
      contentType: 'application/json'
    },
    data
  })
}

// 
function likeApi (id) {
  return _fetch({
    url: `/v1_0/article/dislikes/${id}`,
    method: 'DELETE',
    headers: {
      contentType: 'application/json'
    }
  })
}
// &
function addSendApi (data) {
  return _fetch({
    url: '/v1_0/comments',
    method: 'POST',
    headers: {
      contentType: 'application/json'
    },
    data
  })
}
// 
function addcommsApi (data) {
  return _fetch({
    url: '/v1_0/comment/likings',
    method: 'POST',
    headers: {
      contentType: 'application/json'
    },
    data
  })
}

function deleLikes (id) {
  return _fetch({
    url: `/v1_0/comment/likings/${id}`,
    method: 'DELETE',
    headers: {
      contentType: 'application/json'
    }
  })
}

export {
  getDetailsApi,
  getCommentsApi,
  unfollowUsersApi,
  followUsersApi,
  likingsApi,
  deleteLikingApi,
  distasteApi,
  likeApi,
  addSendApi,
  addcommsApi,
  deleLikes
}
